import React from "react";
import { Link } from "react-router-dom";

const Economy3 = () => {
    return(
        <section>
        <main class="banner container__large">
            <div class="h1">Glosario de Presupuesto Público
            </div>
            <div>
                <mark>#etiqueta1</mark>
                <mark>#etiqueta1</mark>
                <mark>#etiqueta1</mark>
            </div>
            <div class="author">
                <p>👧 <mark class=""> Elizabeth Evelin Peredo Mescco</mark></p>
            </div>
        </main>
        <section class="container__small">
                    <br />
                    <Link class="btn" to='/blog'>Back</Link>
                    <hr />
                </section>
        <section class="container__small">

<table>
    <thead>
        <tr>
            <th>Término</th>
            <th>Definición</th>
        </tr>
    </thead>
    <tbody>
        <tr>
            <td>
                <span >
                    Actividad
                </span>
            </td>
            <td>
                <div>
                    <p>Categoría presupuestaria básica que reúne acciones que concurren
                        en la operatividad y mantenimiento de los servicios públicos o administrativos existentes.
                        Representa la producción de los bienes y servicios que la entidad pública lleva a cabo de
                        acuerdo con sus competencias, dentro de los procesos y tecnologías vigentes. Es permanente y
                        continua en el tiempo. Responde a objetivos que pueden ser medidos cualitativa o
                        cuantitativamente, a través de sus Componentes y Metas.</p>
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Año
                    Fiscal
                </span>
            </td>
            <td>
                <div>
                    Período en que se ejecuta el Presupuesto del Sector Público y que coincide con el año
                    calendario, es decir, se inicia el primero de enero y finaliza el treinta y uno de diciembre.
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Anulación presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Supresión total o parcial de los créditos presupuestarios de Actividades o Proyectos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Aprobación del presupuesto
                </span>
            </td>
            <td>
                <div>
                    Acto por el cual se fija legalmente el total del crédito presupuestario, que comprende el límite
                    máximo de gasto a ejecutarse en el año fiscal. En el caso de los Pliegos del Gobierno Nacional,
                    los créditos presupuestarios se establecen en la Ley Anual de Presupuesto del Sector Público,
                    los mismos que para su ejecución requieren de su formalización a través de la aprobación del
                    Presupuesto Institucional de Apertura. En el caso de los Gobiernos Regionales y Locales, los
                    créditos presupuestarios son establecidos en sus respectivos Presupuestos Institucionales de
                    Apertura considerando los montos que les aprueba la Ley Anual de Presupuesto del Sector Público.
                    En el caso de las Empresas y Organismos Públicos Descentralizados de los Gobiernos Regionales y
                    Gobiernos Locales, los créditos presupuestarios son establecidos mediante Decreto Supremo.
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Avance financiero
                </span>
            </td>
            <td>
                <div>
                    Estado que permite conocer la evolución de la ejecución presupuestal de los ingresos y gastos a
                    un período determinado. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Avance físico
                </span>
            </td>
            <td>
                <div>
                    Estado que permite conocer el grado de cumplimiento de las Metas Presupuestarias aprobadas en
                    los Presupuestos Institucionales de las entidades, a un período determinado. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Cadena de gasto
                </span>
            </td>
            <td>
                <div>
                    Conjunto de elementos expresados en una secuencia numérica que refleja las distintas categorías
                    de la estructura funcional programática y las partidas de gasto de los Clasificadores
                    Presupuestarios del egreso. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Cadena de ingreso
                </span>
            </td>
            <td>
                <div>
                    Conjunto de elementos expresados en una secuencia numérica que refleja las distintas categorías
                    del Clasificador Presupuestario del Ingreso. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Calendario de compromisos institucional
                </span>
            </td>
            <td>
                <div>
                    Acto de administración de programación mensual para hacer efectivo la ejecución de las
                    obligaciones comprometidas y devengadas, con sujeción a la percepción de los ingresos que
                    constituye su financiamiento. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Categoría del gasto
                </span>
            </td>
            <td>
                <div>
                    Elemento de la cadena de gasto que comprende los créditos presupuestarios agrupados en gastos
                    corrientes, gastos de capital y el servicio de la deuda. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Categoría presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Comprende los elementos relacionados a la metodología del presupuesto por programas denominados
                    Programa, Subprograma, Actividad y Proyecto. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Certificación Presupuestal
                </span>
            </td>
            <td>
                <div>
                    Acto de administración, cuya finalidad es garantizar que se cuenta con el crédito presupuestario
                    disponible y libre de afectación, para comprometer un gasto con cargo al presupuesto
                    institucional autorizado para el año fiscal respectivo, previo cumplimiento de las disposiciones
                    legales vigentes que regulen el objeto materia del compromiso. Dicha certificación implica la
                    reserva del crédito presupuestario, hasta el perfeccionamiento del compromiso y la realización
                    del correspondiente registro presupuestario. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Clasificador Funcional del Sector Público
                </span>
            </td>
            <td>
                <div>
                    Constituye una clasificación detallada de las funciones a cargo del Estado y tiene por objeto
                    facilitar el seguimiento, exposición y análisis de las tendencias del gasto público respecto a
                    las principales funciones del Estado, siendo la Función el nivel máximo de agregación de las
                    acciones que ejerce el Estado, el Programa Funcional un nivel detallado de la Función y el
                    Sub-Programa Funcional un nivel detallado del Programa Funcional. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Clasificador Programático del Sector Público
                </span>
            </td>
            <td>
                <div>
                    Está compuesto por Programas Estratégicos que comprenden un conjunto de acciones (actividades
                    y/o proyectos) que expresan una política, con objetivos e indicadores bien definidos, vinculados
                    a un responsable del programa, sujetos a seguimiento y evaluación, en línea con el presupuesto
                    por resultados. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Clasificadores Presupuestarios de Ingresos y Gastos
                </span>
            </td>
            <td>
                <div>
                    Instrumentos técnicos que permiten el registro ordenado y uniforme de las operaciones del Sector
                    Público durante el proceso presupuestario. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Componente
                </span>
            </td>
            <td>
                <div>
                    División de una Actividad o Proyecto que permite identificar un conjunto de acciones
                    presupuestarias concretas. Cada Componente a su vez comprende necesariamente una o más Metas
                    Presupuestarias orientadas a cumplir los Objetivos Específicos de las Actividades o Proyectos
                    previstos por ejecutarse durante el año fiscal. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Compromiso
                </span>
            </td>
            <td>
                <div>
                    Acto mediante el cual se acuerda, luego del cumplimiento de los trámites legalmente
                    establecidos, la realización de gastos previamente aprobados, por un importe determinado o
                    determinable, que afectan total o parcialmente los créditos presupuestarios, en el marco de los
                    presupuestos aprobados y las modificaciones presupuestarias realizadas. El compromiso se efectúa
                    con posterioridad a la generación de la obligación nacida de acuerdo a Ley, Contrato o Convenio.
                    El compromiso debe afectarse preventivamente a la correspondiente cadena de gasto, reduciendo su
                    importe del saldo disponible del crédito presupuestario, a través del respectivo documento
                    oficial. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Contrapartida
                </span>
            </td>
            <td>
                <div>
                    Parte del costo de un proyecto o programa de inversión pública, o actividad, que es financiada
                    con recursos distintos de aquellos derivados de una operación de endeudamiento público o
                    donaciones. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Control presupuestario
                </span>
            </td>
            <td>
                <div>
                    Seguimiento realizado por la Dirección General del Presupuesto Público de los niveles de
                    ejecución de egresos respecto a los créditos presupuestarios autorizados por la Ley Anual de
                    Presupuesto del Sector Público y sus modificatorias. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Convenios de administración por resultados
                </span>
            </td>
            <td>
                <div>
                    Es un acuerdo de carácter técnico suscrito entre las entidades públicas o sus dependencias y la
                    Dirección Nacional del Presupuesto Público. En este acuerdo, la entidad pública se compromete al
                    cumplimiento de un conjunto de Indicadores cuantificables así como al cumplimiento de
                    compromisos orientados a mejorar la cantidad, calidad y cobertura de los bienes que proveen y
                    los servicios que presta. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Cooperación financiera
                </span>
            </td>
            <td>
                <div>
                    Cooperación que, mediante la transferencia monetaria real de fondos al receptor, permite
                    desarrollar proyectos o actividades que beneficien a una institución o país. Puede ser de fondos
                    reembolsables o no reembolsables, dependiendo de si la transferencia debe ser devuelta al país
                    emisor o si la transferencia ha sido efectuada a fondo perdido (donación). </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Cooperación técnica Internacional
                </span>
            </td>
            <td>
                <div>
                    "Medio por el cual se recibe, transfiere y/o intercambia recursos humanos, bienes, servicios,
                    capitales y tecnología de fuentes cooperantes externas; cuyo objetivo es complementar y
                    contribuir con los esfuerzos nacionales para el apoyo de programas y proyectos prioritarios, en
                    armonía con las políticas y planes nacionales, sectoriales, regionales y locales de desarrollo.
                    En el Perú
                    es regulada por el Decreto Legislativo Nº 719 y sus modificatorias, Ley de Cooperación Técnica
                    Internacional." </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Crédito suplementario
                </span>
            </td>
            <td>
                <div>
                    Modificación presupuestaria que incrementa el crédito presupuestario autorizado a la entidad
                    pública, proveniente de mayores recursos respecto a los montos aprobados en el Presupuesto
                    Institucional. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Créditos presupuestarios
                </span>
            </td>
            <td>
                <div>
                    Dotación de recursos consignada en los Presupuestos del Sector Público, con el objeto de que las
                    entidades públicas puedan ejecutar gasto público. Es de carácter limitativo y constituye la
                    autorización máxima de gasto que toda entidad pública puede ejecutar, conforme a las
                    asignaciones individualizadas de gasto, que figuran en los presupuestos, para el cumplimiento de
                    sus objetivos aprobados. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Determinación del ingreso
                </span>
            </td>
            <td>
                <div>
                    Acto por el que se establece o identifica con precisión el concepto, el monto, la oportunidad y
                    la persona natural o jurídica, que debe efectuar un pago o desembolso de fondos a favor de una
                    entidad. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ejecución financiera del ingreso y del gasto
                </span>
            </td>
            <td>
                <div>
                    Proceso de determinación y percepción o recaudación de fondos públicos y, en su caso, la
                    formalización y registro del gasto devengado así como su correspondiente cancelación o pago.
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ejecución presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Etapa del proceso presupuestario en la que se perciben los ingresos y se atienden las
                    obligaciones de gasto de conformidad con los créditos presupuestarios autorizados en los
                    presupuestos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ejecución presupuestaria directa
                </span>
            </td>
            <td>
                <div>
                    Ejecución presupuestal y financiera de las Actividades y Proyectos así como de sus respectivos
                    Componentes a cargo de la entidad pública con su personal e infraestructura. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ejecución presupuestaria indirecta
                </span>
            </td>
            <td>
                <div>
                    Ejecución física o financiera de las Actividades y Proyectos, así como de sus respectivos
                    Componentes, realizada por una entidad distinta a la de origen; sea por efecto de un contrato o
                    convenio celebrado con una entidad privada, o con una entidad pública, sea a título oneroso o
                    gratuito. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ejercicio presupuestario
                </span>
            </td>
            <td>
                <div>
                    Comprende el año fiscal y el periodo de regularización. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Empresa privada
                </span>
            </td>
            <td>
                <div>
                    Empresa cuya propiedad del capital, la gestión, la toma de decisiones y el control son ejercidos
                    por agentes económicos privados; en estas empresas, el Estado no tiene ninguna injerencia.
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Empresa pública
                </span>
            </td>
            <td>
                <div>
                    Empresa cuya propiedad del capital, gestión y toma de decisiones están bajo el control
                    gubernamental. De acuerdo con el giro de la actividad, las empresas públicas pueden ser
                    financieras o no financieras. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Encargo
                </span>
            </td>
            <td>
                <div>
                    Modalidad de la ejecución presupuestaria indirecta, se produce entre entidades del Sector
                    Público cuando por la naturaleza o condiciones en que deba desarrollarse la ejecución física y
                    financiera de las Actividades y/o Proyectos considerados en el Presupuesto Institucional de una
                    Unidad Ejecutora, requiere ser realizado por alguna(s) de sus dependencias desconcentradas o por
                    otra Unidad Ejecutora de un Pliego Presupuestario distinto. Conlleva la suscripción de un
                    convenio entre las entidades intervinientes. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Entidad pública
                </span>
            </td>
            <td>
                <div>
                    Constituye entidad pública para efectos de la Administración Financiera del Sector Público, todo
                    organismo con personería jurídica comprendido en los niveles de Gobierno Nacional, Gobierno
                    Regional y Gobierno Local, incluidos sus respectivos Organismos Públicos Descentralizados y
                    empresas, creados o por crearse; las Sociedades de Beneficencia Pública; los fondos, sean de
                    derecho público o privado cuando este último reciba transferencias de fondos públicos; las
                    empresas en las que el Estado ejerza el control accionario; y los Organismos Constitucionalmente
                    Autónomos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Específica del gasto
                </span>
            </td>
            <td>
                <div>
                    Responde al desagregado del objeto del gasto y se determina según el Clasificador de los Gastos
                    Públicos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Estimación del ingreso
                </span>
            </td>
            <td>
                <div>
                    Cálculo o proyección de los ingresos que por todo concepto se espera alcanzar durante el año
                    fiscal, considerando la normatividad aplicable a cada concepto de ingreso, así como los factores
                    estacionales que incidan en su percepción. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Estructura funcional programática
                </span>
            </td>
            <td>
                <div>
                    "Muestra las líneas de acción que la entidad pública desarrollará durante el año fiscal para
                    lograr los Objetivos Institucionales propuestos, a través del cumplimiento de las Metas
                    contempladas en el Presupuesto Institucional. Se compone de las categorías presupuestarias
                    seleccionadas técnicamente, de manera que permitan visualizar los propósitos por lograr durante
                    el año. Comprende las siguientes categorías:

                    - Función: Corresponde al nivel máximo de agregación de las acciones del Estado, para el
                    cumplimiento de los deberes primordiales constitucionalmente establecidos. La selección de las
                    Funciones a las que sirve el accionar de una entidad pública se fundamenta en su Misión y
                    Propósitos Institucionales.

                    - Programa Funcional: Desagregado de la Función que sistematiza la actuación estatal. A través
                    del Programa se expresan las políticas institucionales sobre las que se determinan las líneas de
                    acción que la entidad pública desarrolla durante el año fiscal. Comprende acciones
                    interdependientes con la finalidad de alcanzar Objetivos Generales de acuerdo con los propósitos
                    de la entidad pública. Los Programas recogen los lineamientos de carácter sectorial e
                    institucional, los que se establecen en función a los objetivos de política general del
                    Gobierno. Los Programas deben servir de enlace entre el planeamiento estratégico y los
                    respectivos presupuestos, debiendo mostrar la dimensión presupuestaria de los Objetivos
                    Generales por alcanzar por la entidad pública para el año fiscal.

                    - Subprograma Funcional: Categoría Presupuestaria que refleja acciones orientadas a alcanzar
                    Objetivos Parciales. Es el desagregado del Programa. Su selección obedece a la especialización
                    que requiera la consecución de los Objetivos Generales a que responde cada Programa determinado.
                    El Subprograma muestra la gestión presupuestaria del Pliego a nivel de Objetivos Parciales.
                    " </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Evaluación presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Fase del proceso presupuestario en la que se realiza la medición de los resultados obtenidos y
                    el análisis de las variaciones físicas y financieras observadas, con relación a lo aprobado en
                    los Presupuestos del Sector Público. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Evaluaciones Independientes
                </span>
            </td>
            <td>
                <div>
                    Producen información sobre el diseño, puesta en práctica y/o resultados de un conjunto de
                    intervenciones públicas evaluadas (IPE). Estas IPE pueden ser programas, actividades,
                    instituciones o líneas de gasto clasificadas dentro del presupuesto del Sector Público. Existen
                    2 tipos de evaluación: (i) Evaluación de Diseño y Ejecución de Intervenciones Públicas â€“ EDEP;
                    y (ii) Evaluación de Impacto â€“ EI. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Fondos públicos
                </span>
            </td>
            <td>
                <div>
                    Todos los recursos financieros de carácter tributario y no tributario que se generan, obtienen u
                    originan en la producción o prestación de bienes y servicios que las Unidades Ejecutoras o
                    entidades públicas realizan, con arreglo a Ley. Se orientan a la atención de los gastos del
                    presupuesto público. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Formulación presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Fase del proceso presupuestario en la cual las entidades públicas definen la Estructura
                    Funcional Programática de su Presupuesto Institucional consistente con los Objetivos
                    Institucionales; seleccionan las Metas Presupuestarias propuestas durante la fase de
                    programación; y consignan las cadenas de gasto, los montos para comprometer gastos (créditos
                    presupuestarios) y las respectivas fuentes de financiamiento. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Fuentes de Financiamiento
                </span>
            </td>
            <td>
                <div>
                    Clasificación presupuestaria de los recursos públicos, orientada a agrupar los fondos de acuerdo
                    con los elementos comunes a cada tipo de recurso. Su nomenclatura y definición están definidas
                    en el Clasificador de Fuentes de Financiamiento para cada año fiscal. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    corriente
                </span>
            </td>
            <td>
                <div>
                    Comprende las erogaciones destinadas a las operaciones de producción de bienes y prestación de
                    servicios, tales como gastos de consumo y gestión operativa, servicios básicos, prestaciones de
                    la seguridad social, gastos financieros y otros. Están destinados a la gestión operativa de la
                    entidad pública durante la vigencia del año fiscal y se consumen en dicho período. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    de capital
                </span>
            </td>
            <td>
                <div>
                    Erogaciones destinadas a la adquisición o producción de activos tangibles e intangibles y a
                    inversiones financieras en la entidad pública, que incrementan el activo del Sector Público y
                    sirven como instrumentos para la producción de bienes y servicios. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    devengado
                </span>
            </td>
            <td>
                <div>
                    Reconocimiento de una obligación de pago derivado del gasto comprometido previamente registrado.
                    Se formaliza a través de la conformidad del área correspondiente en la entidad pública o Unidad
                    Ejecutora que corresponda respecto de la recepción satisfactoria de los bienes y la prestación
                    de los servicios solicitados y se registra sobre la base de la respectiva documentación
                    sustentatoria. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    girado
                </span>
            </td>
            <td>
                <div>
                    Proceso que consiste en el registro del giro efectuado sea mediante la emisión del cheque, la
                    carta orden o la transferencia electrónica con cargo a la correspondiente cuenta bancaria para
                    el pago parcial o total de un gasto devengado debidamente formalizado y registrado. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    pagado
                </span>
            </td>
            <td>
                <div>
                    Proceso que consiste en la efectivización del cheque emitido, la carta orden y la transferencia
                    electrónica; se sustenta con el cargo en la correspondiente cuenta bancaria. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    público
                </span>
            </td>
            <td>
                <div>
                    Conjunto de erogaciones que se realizan en el ámbito del Sector Público. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Gasto
                    social
                </span>
            </td>
            <td>
                <div>
                    Parte del gasto público destinado a financiar servicios sociales básicos para los individuos.
                    Según la clasificación propuesta por las Naciones Unidas, son los gastos de educación, sanidad,
                    seguridad social, vivienda y otros de similares características. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Gastos tributarios
                </span>
            </td>
            <td>
                <div>
                    Constituye el monto cuantificado de las exenciones de la base tributaria, deducciones
                    autorizadas de la renta bruta, créditos fiscales deducidos de los impuestos por pagar,
                    reducciones de las tasas impositivas e impuestos diferidos; tienen un impacto en los fondos
                    públicos que financian los presupuestos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Genérica de gasto
                </span>
            </td>
            <td>
                <div>
                    Nivel mayor de agregación que identifica el conjunto homogéneo, claro y ordenado de los de los
                    gastos en recursos humanos, materiales, tecnológicos y financieros, así como los bienes,
                    servicios y obras públicas que las entidades públicas contratan, adquieren o realizan para la
                    consecución de sus objetivos institucionales. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Genérica de Ingreso
                </span>
            </td>
            <td>
                <div>
                    Nivel mayor de agregación que identifica el conjunto homogéneo, claro y ordenado de los recursos
                    que se recaudan captan y obtienen. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Gestión presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Capacidad de las entidades públicas para lograr sus Objetivos Institucionales, mediante el
                    cumplimiento de las Metas Presupuestarias establecidas para un determinado año fiscal, aplicando
                    los criterios de eficiencia, eficacia y desempeño. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Habilitación presupuestaria
                </span>
            </td>
            <td>
                <div>
                    Incremento de los créditos presupuestarios de Actividades y Proyectos con cargo a anulaciones de
                    la misma Actividad o Proyecto, o de otras Actividades y Proyectos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ingresos corrientes
                </span>
            </td>
            <td>
                <div>
                    Ingresos que se obtienen de modo regular o periódico y que no alteran de manera inmediata la
                    situación patrimonial del Estado. Agrupan los recursos provenientes de tributos, venta de
                    bienes, prestación de servicios, rentas de la propiedad, multas, sanciones y otros ingresos
                    corrientes. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ingresos de capital
                </span>
            </td>
            <td>
                <div>
                    Recursos financieros que se obtienen de modo eventual y que alteran la situación patrimonial del
                    Estado. Agrupan los recursos provenientes de la venta de activos (inmuebles, terrenos,
                    maquinarias, entre otros), las amortizaciones por los préstamos concedidos (reembolsos), la
                    venta de acciones del Estado en empresas, y otros ingresos de capital. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ingresos financieros
                </span>
            </td>
            <td>
                <div>
                    Ingresos obtenidos por una entidad, provenientes de las inversiones financieras o de depósitos
                    efectuados en moneda nacional o extranjera, y de la prestación de servicios de crédito. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Ingresos públicos
                </span>
            </td>
            <td>
                <div>
                    Expresión monetaria de los valores recibidos, causados o producidos por concepto de ingresos
                    corrientes, venta de bienes y servicios, transferencias y otros, en el desarrollo de la
                    actividad financiera, económica y social de la entidad pública. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Inversión pública
                </span>
            </td>
            <td>
                <div>
                    Toda erogación de recursos de origen público destinada a crear, incrementar, mejorar o reponer
                    las existencias de capital físico de dominio público, con el objeto de ampliar la capacidad del
                    país para la prestación de servicios y producción de bienes. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Maestro del Clasificador de Ingresos y Financiamiento
                </span>
            </td>
            <td>
                <div>
                    Contiene el detalle de las partidas que las entidades públicas deben tomar en cuenta para
                    efectuar el registro ordenado de los ingresos por las distintas fuentes de financiamiento.
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Meta
                    Presupuestaria o Meta
                </span>
            </td>
            <td>
                <div>
                    "Expresión concreta y cuantificable que caracteriza el producto o productos finales de las
                    Actividades y Proyectos establecidos para el año fiscal. Se compone de cuatro elementos:

                    (i) Finalidad (objeto preciso de la Meta).

                    (ii) Unidad de medida (magnitud que se utiliza para su medición).

                    (iii) Cantidad (número de unidades de medida que se espera alcanzar).

                    (iv) Ubicación geográfica (ámbito distrital donde se ha previsto la Meta).

                    La Meta Presupuestaria -dependiendo del objeto de análisis- puede mostrar las siguientes
                    variantes:

                    a) Meta Presupuestaria de Apertura: Meta Presupuestaria considerada en el Presupuesto
                    Institucional de Apertura.

                    b) Meta Presupuestaria Modificada: Meta Presupuestaria cuya determinación es considerada durante
                    un año fiscal. Se incluye en este concepto a las Metas Presupuestarias de Apertura y las nuevas
                    Metas que se agreguen durante el año fiscal.

                    c) Meta Presupuestaria Obtenida: Estado situacional de la Meta Presupuestaria en un momento
                    dado.
                    " </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Modificaciones presupuestarias
                </span>
            </td>
            <td>
                <div>
                    Constituyen cambios en los créditos presupuestarios, tanto en su cuantía como en el nivel
                    Institucional (créditos suplementarios y transferencias de partidas) y, en su caso, a nivel
                    funcional programático (habilitaciones y anulaciones). Las modificaciones presupuestarias pueden
                    afectar la estructura funcional-programática a consecuencia de la supresión o incorporación de
                    nuevas metas presupuestarias. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Pago
                    anual por mantenimiento y operación (PAMO)
                </span>
            </td>
            <td>
                <div>
                    Pago anual por concepto de mantenimiento y operación de un bien público que el Estado efectuará
                    al concesionario durante todo el período de la concesión, de acuerdo con los términos indicados
                    en el respectivo contrato de concesión. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Pago
                    anual por obras (PAO)
                </span>
            </td>
            <td>
                <div>
                    Pago anual por obras de construcción que el Estado pagará al concesionario una vez que haya
                    recibido la obra concluida en los términos indicados en el respectivo contrato de concesión.
                </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Percepción del ingreso
                </span>
            </td>
            <td>
                <div>
                    Momento en el cual se produce la recaudación, captación u obtención efectiva del ingreso. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Período de regularización
                </span>
            </td>
            <td>
                <div>
                    Período en el que se complementa el registro de la información de ingresos y gastos de las
                    entidades públicas sin excepción. No puede exceder el 31 de marzo de cada año. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Pliego Presupuestario
                </span>
            </td>
            <td>
                <div>
                    Toda entidad pública que recibe un crédito presupuestario en la Ley Anual de Presupuesto del
                    Sector Público. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Presupuesto Institucional de Apertura (PIA)
                </span>
            </td>
            <td>
                <div>
                    Presupuesto inicial de la entidad pública aprobado por su respectivo Titular con cargo a los
                    créditos presupuestarios establecidos en la Ley Anual de Presupuesto del Sector Público para el
                    año fiscal respectivo. En el caso de las Empresas y Organismos Públicos Descentralizados de los
                    Gobiernos Regionales y Gobiernos Locales, los créditos presupuestarios son establecidos mediante
                    Decreto Supremo. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Presupuesto Institucional Modificado (PIM)
                </span>
            </td>
            <td>
                <div>
                    Presupuesto actualizado de la entidad pública a consecuencia de las modificaciones
                    presupuestarias, tanto a nivel institucional como a nivel funcional programático, efectuadas
                    durante el año fiscal, a partir del PIA. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Presupuesto Multianual de la Inversión Pública
                </span>
            </td>
            <td>
                <div>
                    Constituye un marco referencial, Instrumento del proceso presupuestario, que contiene la
                    distribución de los recursos del Estado por un periodo más allá del año fiscal, enmarcado en el
                    plan estratégico del gobierno y el Marco Macroeconómico Multianual. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Presupuesto Participativo
                </span>
            </td>
            <td>
                <div>
                    Instrumento de política y de gestión, a través del cual las autoridades regionales y locales,
                    así como las organizaciones de la población debidamente representadas, definen en conjunto, cómo
                    y a qué se van a orientar los recursos, los cuales están directamente vinculados a la visión y
                    objetivos del Plan de Desarrollo Concertado. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Presupuesto por Resultados
                </span>
            </td>
            <td>
                <div>
                    El Presupuesto por Resultados (PpR) es una metodología que se aplica progresivamente al proceso
                    presupuestario y que integra la programación, formulación, aprobación, ejecución y evaluación
                    del presupuesto, en una visión de logro de productos, resultados y uso eficaz y eficiente de los
                    recursos del Estado a favor de la población, retroalimentando los procesos anuales de asignación
                    del presupuesto público y mejorando los sistemas de gestión administrativa del Estado. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Presupuestos del Sector Público
                </span>
            </td>
            <td>
                <div>
                    Constituidos por la Ley Anual de Presupuesto del Sector Público aprobada por el Congreso de la
                    República; los presupuestos de los Gobiernos Regionales y los Gobiernos Locales aprobados en el
                    marco de su autonomía constitucional; el presupuesto consolidado de las empresas sujetas al
                    Fondo Nacional de Financiamiento de la Actividad Empresarial del Estado (FONAFE); y el
                    presupuesto de la seguridad social de salud (ESSALUD). Asimismo, comprende el presupuesto
                    consolidado de las empresas municipales y los organismos públicos descentralizados de los
                    Gobiernos Regionales y Locales. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Programación del ingreso y del gasto
                </span>
            </td>
            <td>
                <div>
                    Proceso técnico que tiene por finalidad determinar el comportamiento probable de los niveles de
                    ejecución (mensual y trimestral) de los ingresos y gastos de un determinado año fiscal. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Programación presupuestaria en las entidades
                </span>
            </td>
            <td>
                <div>
                    Fase del proceso presupuestario en la cual las entidades públicas determinan la escala de
                    prioridades de los Objetivos Institucionales del año fiscal; determinan la demanda global de
                    gasto y las Metas Presupuestarias compatibles con sus Objetivos Institucionales y funciones;
                    desarrollan los procesos para la estimación de los fondos públicos con el objeto de determinar
                    el monto de la asignación presupuestaria; y definen la estructura del financiamiento de la
                    demanda global de gasto en función de la estimación de los fondos públicos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Programación Presupuestaria Estratégica
                </span>
            </td>
            <td>
                <div>
                    "Proceso en el que se relacionan los resultados con los productos, acciones y medios necesarios
                    para su ejecución, los cuales se basan en información y experiencia existente (evidencias). Este
                    proceso se realiza con la finalidad de solucionar un problema crítico que afecta a una
                    determinada población (condición de interés).

                    Se aplica en las fases de programación y formulación presupuestal y comprende el diseño de
                    estrategias e intervenciones articuladas del Estado para la generación de cambios específicos en
                    alguna condición de bienestar del ciudadano. Estos cambios son los resultados.

                    El producto concreto de la aplicación de la PPE es el Programa Estratégico (PE), que bajo la
                    óptica de mejorar el bienestar ciudadano, provee un conjunto de insumos, productos y resultados
                    articulados causalmente. La particularidad del PE es que se diseña, en sus etapas iniciales,
                    independientemente de los que las instituciones ya vienen haciendo, y con el objeto de definir
                    una hoja de ruta que, bajo el enfoque del uso de evidencias, permite proveer bienes y servicios
                    públicos para lograr el resultado deseado." </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Proyecto
                </span>
            </td>
            <td>
                <div>
                    Conjunto de intervenciones limitadas en el tiempo, de las cuales resulta un producto final
                    (Metas Presupuestarias), que concurre a la expansión de la acción del Gobierno. Representa la
                    creación, ampliación, mejora, modernización y/o recuperación de la capacidad de producción de
                    bienes y servicios, implicando la variación sustancial o el cambio de procesos y/o tecnología
                    utilizada por la entidad pública. Luego de su culminación, generalmente se integra o da origen a
                    una Actividad. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Recursos Públicos
                </span>
            </td>
            <td>
                <div>
                    Recursos del Estado inherentes a su acción y atributos que sirven para financiar los gastos de
                    los presupuestos anuales y se clasifican a nivel de fuentes de financiamiento. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Reserva de contingencia
                </span>
            </td>
            <td>
                <div>
                    Crédito presupuestario global dentro del Presupuesto del Pliego Ministerio de Economía y
                    Finanzas, destinado a financiar los gastos que por su naturaleza y coyuntura no han sido
                    previstos en los Presupuestos de los Pliegos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span > Rubro
                </span>
            </td>
            <td>
                <div>
                    Título con el cual se designa un grupo de partidas o de cuentas contables. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Servicio de deuda
                </span>
            </td>
            <td>
                <div>
                    Monto de obligaciones por concepto del capital o principal de un préstamo que se encuentra
                    pendiente de pago, así como de los intereses, comisiones y otros derivados de la utilización del
                    préstamo, que se debe cancelar periódicamente según lo acordado en el respectivo Contrato de
                    Préstamo. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Transferencia de partidas
                </span>
            </td>
            <td>
                <div>
                    Modificación presupuestaria que puede efectuarse en el Nivel Institucional, es decir, constituye
                    traslados de créditos presupuestarios entre Pliegos. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Transferencias financieras entre Pliegos Presupuestarios
                </span>
            </td>
            <td>
                <div>
                    Traspasos de fondos públicos sin contraprestación, para la ejecución de Actividades y Proyectos
                    de los Presupuestos Institucionales respectivos de los Pliegos de destino. </div>

            </td>
        </tr>
        <tr>
            <td>
                <span >
                    Unidad ejecutora
                </span>
            </td>
            <td>
                <div>
                    Constituye el nivel descentralizado u operativo en las entidades públicas. Una Unidad Ejecutora
                    cuenta con un nivel de desconcentración administrativa que: Determina y recauda ingresos;
                    contrae compromisos, devenga gastos y ordena pagos con arreglo a la legislación aplicable;
                    registra la información generada por las acciones y operaciones realizadas; informa sobre el
                    avance y/o cumplimiento de metas; recibe y ejecuta desembolsos de operaciones de endeudamiento;
                    y/o se encarga de emitir y/o colocar obligaciones de deuda. </div>

            </td>
        </tr>
    </tbody>
</table>
</section>

        </section>
    );
    
}


export default Economy3;